*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.header{height:45px;display:flex;justify-content:space-between;align-items:center;height:16vh;padding:1rem 3rem;background-color:#16181b}.header__menu{display:none}.header__logo{width:40px;height:40px;object-fit:contain}.header form{display:flex;flex:0.6 1;padding:5px;margin:0 12px;background-color:#121417;border-radius:6px;border:1px solid;font-size:19px}.header form input{width:100%;border:none;padding:.3rem;background:transparent;color:#b1bdb4}.header form input:focus{outline:none}.header form button{padding:2px 1.25rem;color:#b1bdb4;border:none;background:transparent}.header form button:focus{outline:none}.header__icons{display:flex;flex:0.15 1;justify-content:space-around;align-items:center}.header__icons img{width:35px;object-fit:contain;margin-left:3px;border-radius:50%}@media (max-width: 720px){.header{padding:1.3rem}.header form{flex:.7 1}.header__menu{display:block}.header__logo{display:none}.header__icons>*:not(img){display:none}}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.category{width:100%;display:flex;padding:6px;overflow-x:scroll;font-size:14px;position:fixed;z-index:99;background:#121417;top:56px;margin-left:-12px}.category__element{padding:8px 13px;margin-right:13px;white-space:nowrap;border:1.5px solid #b1bdb4;border-radius:25px;cursor:pointer;margin-top:1%}.category__element:hover{background:#1a4033}.category__element.active{color:#fff;background:#303a3a;border-color:#4c4c4c}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.video{margin-bottom:1rem;font-weight:500;font-size:0.9rem;cursor:pointer}.video__top{margin-bottom:0.5rem;position:relative}.video__top img{width:100%;border-radius:5px}.video__top span{position:absolute;bottom:6px;right:3px;background:#080808ec;border-radius:3px;padding:1px 2px}.video__title{overflow:hidden;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;margin-bottom:0.1rem;color:#fff}.video__details{display:flex;align-items:center}.video__channel{display:flex;align-items:center;margin:0.5rem 0}.video__channel img{width:36px;height:36px;border-radius:50%;margin-right:0.5rem;cursor:pointer}.video__channel p{margin-bottom:0}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.home_screen{width:100%;height:100%;padding:12px}.video-grid{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:24px;margin-top:85px}@media (max-width: 990px){.video-grid{grid-template-columns:repeat(3, 1fr);grid-gap:22px}}@media (max-width: 720px){.video-grid{grid-template-columns:repeat(2, 1fr)}}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.sidebar{width:211px;background-color:#121417;display:flex;flex-direction:column;transition:transform 0.2s ease-in;padding-top:2rem}.sidebar li{display:flex;align-items:center;cursor:pointer;padding:0.6rem 1.5rem;margin:9px 0}.sidebar li span{margin-left:1rem;font-size:14px;font-weight:500;letter-spacing:0.4px}.sidebar li:hover{background:#4c4c4c}.sidebar hr{background:#4c4c4c}.sidebar.open{transform:translateX(0)}@media (max-width: 990px){.sidebar{width:90px}.sidebar li{justify-content:center}.sidebar span{display:none}}@media (max-width: 720px){.sidebar{transform:translateX(-100%);position:fixed}}

.app__container{display:flex;height:90vh}.app__main{overflow-y:scroll;width:100%;height:100%}

